import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Router} from "@angular/router";
import { RequestService } from '../request.service';
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

    name="";
    college="";
    password="";
    email="";
    address="";
    contactno="";
    error="";
    success="";
    

    constructor(private http: HttpClient,private request: RequestService,private router: Router) { }

  ngOnInit() {
  }

  validateEmail(email) {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }

    signup(){
      
      this.error="";
      if(this.name=="")
        this.error+="Name field is empty";
      else if(this.email=="")
        this.error+="Email Field is empty";
      else if(this.validateEmail(this.email)==false)
        this.error="Email is not valid!!!";
      else if(this.password=="")
        this.error+="Password Field is empty";
      else if(this.college=="")
        this.error+="College Field is empty";
      else if(this.address=="")
        this.error+="Address Field is empty";
      else if(this.contactno=="")
        this.error+="Contact No: Field Is Empty";
        else if(this.contactno.length!=10)
        this.error+="Contact No: must be of 10 digits!!";
      
        if(this.error!="")
        {
         var  v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

        if (v.length != 0) 
        v[0].style.display="block";
        
      }

      else{
        
        v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

        if (v.length != 0) 
        v[0].style.display="none";

      alert('Succesfully Signed Up');
       

      const headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var p={name: this.name, email:this.email, password: this.password,college:this.college,address:this.address,contactno:this.contactno };
    console.log(JSON.stringify(p));
    this.http.post("http://localhost:2000/signup",JSON.stringify(p),{headers:headers}).subscribe((data)=>{
        localStorage.setItem('token',data['token']);
        localStorage.setItem('name',this.name);
      this.request.z=true;
      this.router.navigate(['listings']);
    });
   
   


      }
    }

    login(){

      this.router.navigate(['/login']);
    }
    
}
