import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import { RequestService } from '../request.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  
  zo=false;
  name="";
  searchValue="";

  constructor(private router: Router,private request: RequestService,private http: HttpClient) {

  }

  ngOnInit() {
    this.zo=this.request.z;
    console.log("asdsadas" + this.zo);
    this.request.check(localStorage.getItem("token"),localStorage.getItem("name")).then( (result) => {
      console.log(result);
      console.log("In nginit " + result);
    if(result===true)
        this.request.z=false;
      else
        this.request.z=true;
      this.zo=this.request.z;
      if(this.request.z){

      console.log("getting name");
      var p={token: localStorage.getItem("token")};
      const headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    this.http.post("http://localhost:2000/getName",JSON.stringify(p),{headers:headers}).subscribe( (data)=>{
        this.name=data["name"];

    });
    

      }
    });
    

  }
  logout(){
    console.log("logout");
    localStorage.setItem("name","");
    localStorage.setItem("token","");
    this.request.z=false;
    this.zo=this.request.z;
    this.router.navigate(['/login']);
  }

  search(){
    //SEARCH NOT WORKING ON SEARCH PAGE
    console.log("In search");
    this.request.search=this.searchValue;
    console.log(this.request.search);
    this.router.navigate(['search']);


  }

  




}
