import { Component, OnInit } from '@angular/core';
//import { FormsModule } from '@angular/forms'; // <-- NgModel lives here
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Router} from "@angular/router";
import { RequestService } from '../request.service';
@Component({
  selector: 'app-additem',
  templateUrl: './additem.component.html',
  styleUrls: ['./additem.component.css']
})
export class AdditemComponent implements OnInit {
  error="";
  success="";
  selectedFile: File;
  name='';
  price='';
  author='';
  condition="New";
  url='';
  user_name="";
  description='';
  constructor(private http: HttpClient,private request: RequestService,private router: Router) { }

  ngOnInit() {
    //console.log(this.condition);

    console.log("getting name");
      var p={token: localStorage.getItem("token")};
      const headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    this.http.post("http://localhost:2000/getName",JSON.stringify(p),{headers:headers}).subscribe( (data)=>{
        this.user_name=data["name"];

    });
  }
  
 
  submit(){

    this.error="";
    if(this.name=="")
      this.error+="Book name field is empty";
    else if(this.author=="")
      this.error+="Author field is empty";
      else if(this.url=="")
      this.error+="URL field is empty";
    else if(this.price=="")
      this.error+="Price field is empty";
    else if(this.description=="")
      this.error+="Description field is empty";

    if(this.error!=""){

      let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";

    }
    else{

      this.success="Item Added Successfully";
      let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="none";

         v = document.getElementsByClassName('success') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";
    var p={name: this.name, author: this.author, price: this.price, condition: this.condition,description: this.description,seller: this.user_name,url: this.url};
    
    const headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    this.http.post("http://localhost:2000/addItem",JSON.stringify(p),{headers:headers}).subscribe( (data)=>{
        
        
    });
    console.log("Item added");
    alert("Item added");
        this.router.navigate(['listings']);
  }
  }
 

}
