import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { RequestService } from '../request.service';
import {Router} from "@angular/router";
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
    error="";
    success="";
    email="";
    password="";
    id="";
    check=false;
    constructor(private http: HttpClient,private request: RequestService,private router: Router) { }

  ngOnInit() {
    //localStorage.removeItem("id");
    
      /*this.request.check(localStorage.getItem("token"),localStorage.getItem("name")).then( (result) => {
        console.log("In nginit" + result);

      if(result)
        this.router.navigate(['/signup']);;
      });*/
      


  }

  validateEmail(email) {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }
  
  login(){

    this.error="";
    if(this.email=="")
      this.error='Email Field Is empty';
      else if(this.validateEmail(this.email)==false)
        this.error="Email is not valid!!!";
    else if(this.password=="")
      this.error+="Password Field Is Empty";
    if(this.error!="")
    {
      let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";

    }

  else{

    const headers = new HttpHeaders()
  .set('Authorization', 'my-auth-token')
  .set('Content-Type', 'application/json');
  var p={email:this.email, password: this.password};
  console.log(JSON.stringify(p));
  this.http.post("http://localhost:2000/login",JSON.stringify(p),{headers:headers}).subscribe( (data) => {

      if(data["token"]==""){
        
        this.error+='No Record Found';
        let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";

      }
      else{
        this.success="Successfully Logged In!!!";
        var  v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="none";

        v = document.getElementsByClassName('success') as HTMLCollectionOf<HTMLElement>;

        if (v.length != 0) 
        v[0].style.display="block";

      
        console.log("Logged in");
        console.log(data['name']);
        console.log(data["token"]);
        localStorage.setItem("token",data["token"]);
        localStorage.setItem("name",data['name']);
        this.request.z=true;
        this.router.navigate(['listings']);
        
      }
  });
}

  }

  /*change(){
    this.error='Invalid';
  }*/


  register(){
    this.router.navigate(['/signup']);
  }
}
