import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { RequestService } from '../request.service';
import {Router} from "@angular/router";
@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  received=true;
  user_id;
  messages;
  message="";
  name="";
  success="";
  error="";
  constructor(private http: HttpClient,private request: RequestService,private router: Router) { }

  ngOnInit() {
   
    var headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var object={token: localStorage.getItem("token")};

    this.http.post("http://localhost:2000/getName",JSON.stringify(object),{headers:headers}).subscribe( (data) => {

        this.user_id=data['user_id'];
        this.name=data["name"];
        console.log(this.user_id);
        var headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var object={id:this.user_id};

    this.http.post("http://localhost:2000/recievedMessages",JSON.stringify(object),{headers:headers}).subscribe( (data) => {
          this.messages=data["messages"];
          console.log(this.messages);
  });
  });
  }

  sent(){
    console.log('asikasi');
    this.received=false;
    console.log(this.received);
    var headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var object={id:this.user_id};
    console.log(this.user_id);
    this.http.post("http://localhost:2000/sentMessages",JSON.stringify(object),{headers:headers}).subscribe( (data) => {
          this.messages=data["messages"];
          console.log(this.messages);
          
  });
  }
  receive(){
    console.log('aaa');
    this.received=true;
    var headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var object={id:this.user_id};
    console.log(this.user_id);
    this.http.post("http://localhost:2000/recievedMessages",JSON.stringify(object),{headers:headers}).subscribe( (data) => {
          this.messages=data["messages"];
          console.log(this.messages[0].datetime);
  });
  }

  reply=(rename,reciever_id,id,bookname,path)=>{
    console.log("reply" + this.message);
  
    if(this.message=="")
      this.error="Message Field Is Empty";
    if(this.error!=""){
     
      let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";

    }
    else{
      this.success="Message Sent successfully";
      let v = document.getElementsByClassName('error') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="none";

        v = document.getElementsByClassName('success') as HTMLCollectionOf<HTMLElement>;

      if (v.length != 0) 
        v[0].style.display="block";

    var headers = new HttpHeaders()
    .set('Authorization', 'my-auth-token')
    .set('Content-Type', 'application/json');
    var object={sname:this.name,rname:rename,message:this.message,seller_id: reciever_id,user_id: this.user_id,product_id: id,book_name:bookname,url: ""};
    this.http.post("http://localhost:2000/sendMessage",JSON.stringify(object),{headers:headers}).subscribe( (data) => {
          this.received=false;
          
  });
    }
  }

  

}
